@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Anton);
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family: 'Lato', 'Helvetica Neue', sans-serif;
  background-color: #F4F4F7;
}

h1 {
  font-family: 'Anton', sans-serif;
}

a.link-unstyled {
  color: inherit;
}

a.link-unstyled:hover {
  color: inherit;
  text-decoration: none;
}

.btn {
  padding: 0.375rem 2rem;
}

.btn.btn-primary {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(125, 205, 64, 0.5);
}

.btn.btn-danger {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(255, 64, 64, 0.5);
}
.Badges__hero {
    width: 100%;
    padding: 2rem 0;
    background: url(/platziBadges/static/media/stars.c24e6dd1.svg), #1B1B25;
    background-repeat: repeat;
    margin-bottom: 1rem;
  }
  
  .Badges__hero>.Badges__container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
  }
  
  .Badges__container {
    display: grid;
    grid-template-columns:repeat(auto-fill,minmax(300px,1fr));
    grid-gap :20px;
    gap :20px;
    width: 100%;
    margin:0 auto;
    margin-bottom: 20px;
    padding: 0 1rem;
    list-style: none;
  }
  .Loader img{
    width: 100%;
    text-align: center;
  }
  
  .Badges_conf-logo {
    margin-bottom: 2rem;
  }
  
  .Badges__buttons {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    margin-bottom: 1rem;
    margin: 0 auto;
    width: 80%;
    margin-bottom: 10px;
  }
  @media screen and (min-width:800px){
    .Badges__container {
      grid-template-columns:repeat(auto-fit,minmax(300px,1fr));
    }
  }
.BadgeListItem__container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1px;
    gap: 1px;
    -webkit-align-items: center;
            align-items: center;
    justify-items: center;
    border: 1px solid grey;
    border-radius: 10px;
    box-shadow: 1px 2px 3px 1px grey;
    overflow: hidden;
}
.BadgeListItem__info{
    justify-self: flex-start;
}
.BadgeListItem__info > h1{
    font-size: 1.5rem;
}
.BadgeListItem__img{
    width: 100%;
    object-fit: contain;
    border-radius: 50%;
}
