.BadgeListItem__container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1px;
    align-items: center;
    justify-items: center;
    border: 1px solid grey;
    border-radius: 10px;
    box-shadow: 1px 2px 3px 1px grey;
    overflow: hidden;
}
.BadgeListItem__info{
    justify-self: flex-start;
}
.BadgeListItem__info > h1{
    font-size: 1.5rem;
}
.BadgeListItem__img{
    width: 100%;
    object-fit: contain;
    border-radius: 50%;
}
