.Badges__hero {
    width: 100%;
    padding: 2rem 0;
    background: url('../../images/stars.svg'), #1B1B25;
    background-repeat: repeat;
    margin-bottom: 1rem;
  }
  
  .Badges__hero>.Badges__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .Badges__container {
    display: grid;
    grid-template-columns:repeat(auto-fill,minmax(300px,1fr));
    gap :20px;
    width: 100%;
    margin:0 auto;
    margin-bottom: 20px;
    padding: 0 1rem;
    list-style: none;
  }
  .Loader img{
    width: 100%;
    text-align: center;
  }
  
  .Badges_conf-logo {
    margin-bottom: 2rem;
  }
  
  .Badges__buttons {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 1rem;
    margin: 0 auto;
    width: 80%;
    margin-bottom: 10px;
  }
  @media screen and (min-width:800px){
    .Badges__container {
      grid-template-columns:repeat(auto-fit,minmax(300px,1fr));
    }
  }